/* Base style */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #f5f5f5;
}

img {
    width: 100%;
    max-width: 100%;
    display: blo—Åk;
}

a {
    color: #ec407a;
    display: inline-block;
    border-radius: 6px;
    overflow: hidden;
    width: 100%;
    min-height: 100px;
}

a:hover {
    color: #e91e63;
}

button {
    display: block;
    border: none;
    color: #fff;
    cursor: pointer;
    transition: 0.2s background-color;
}

button:disabled {
    background-color: #cfd8dc;
}

button:disabled:hover {
    background-color: #cfd8dc;
}

main {
    max-width: 1400px;
    min-height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 50px;
    background-color: #fff;
    box-sizing: border-box;
    border-radius: 6px;
    margin: 50px auto;
}

/* Action style */
.action {
    background-color: #66bb6a;
    font-size: 24px;
    padding: 10px 20px;
    border-radius: 6px;
    margin: 10px 0;
}

.action:hover {
    background-color: #4caf50;
}

/* Images style */
.content {
    position: relative;
    padding: 20px;
    margin-bottom: 20px;
    column-count: 4;
    column-gap: 5px;
    min-height: 400px;
    border-radius: 6px;
}

a:nth-child(n) {
    background-color: #efebe9;
}

a:nth-child(2n) {
    background-color: #d7ccc8;
}

a:nth-child(3n) {
    background-color: #bcaaa4;
}

/* Popup style */
.popup {
    position: fixed;
    visibility: hidden;
    display: flex;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(33, 33, 33, 0.5);
    z-index: 2;
}

.popup.open {
    visibility: visible;
}

.popup .content {
    text-align: right;
    column-count: initial;
    width: 100%;
    margin: auto;
    box-sizing: border-box;
    max-width: 600px;
    background-color: #fff;
    border-radius: 6px;
    padding: 20px;
}

.popup .link {
    display: inline;
}

figure {
    max-width: 100%;
    min-height: 350px;
    margin: 0;
}

figure img {
    border-radius: 6px;
}

figcaption {
    color: #424242;
    margin: 5px 0;
}
